.header__menu {
  display: grid;
}

@media screen and (width < 880px) {
  .header__menu {
    grid-column: span 2;
    grid-template-rows: 0fr;
    transition: grid-template-rows 250ms ease-in-out;
    transition: grid-template-rows 250ms ease-in-out, -ms-grid-rows 250ms ease-in-out;
    grid-row-start: 1;
  }
}